<div class={classes}
     class:closed class:dismissible
     role="alert">
  <slot />
  {#if dismissible}
    <span class="btn-close" on:click={close}>X</span>
  {/if}
</div>

<script lang="ts">;
import { computeClasses } from '../utils';
export let type = 'primary';
export let dismissible = false;
let closed = false;
function close() {
    closed = true;
}
let classes;
$: classes = `alert ${computeClasses('alert', { type })}`;
</script>

<style lang="scss">.alert {
  transition: all 0.25s ease-out;
}
.alert.closed {
  border-width: 0;
  margin: 0;
  max-height: 0;
  opacity: 0;
  padding-bottom: 0;
  padding-top: 0;
}</style>
